import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBSelect,
} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";

export default function Step1(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const formRef = new React.createRef();

  const validate = () => {
    setTouched(true);
    const isValid = formRef.current.checkValidity();
    setErrorMsg(!isValid ? "Please fill out all fields properly" : "");
  };
  useEffect(() => {
    setTouched(formRef.current.checkValidity());
  }, []);
  return (
    <Step heading={"I just need the details of your other parent too"}>
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <MDBContainer>
        <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
          <MDBRow center>
            <MDBCol sm={10} md={8}>
              <MDBRow>
                <MDBCol md={6}>
                  <MDBInput
                    color="pink"
                    label="First Name"
                    group
                    type="text"
                    outline
                    name="parent2firstName"
                    pattern="^[a-zA-Z ][a-zA-Z\\s ]+$"
                    value={state.parent2firstName}
                    onChange={(e) => {
                      validate();
                      onChangeField(e, e.target.name, e.target.value);
                    }}
                    required
                    containerClass="mb-0"
                  />
                </MDBCol>
                <MDBCol md={6}>
                  <MDBInput
                    label="Last Name"
                    group
                    outline
                    type="text"
                    name="parent2lastName"
                    pattern="^[a-zA-Z ][a-zA-Z\\s ]+$"
                    value={state.parent2lastName}
                    onChange={(e) => {
                      validate();
                      onChangeField(e, e.target.name, e.target.value);
                    }}
                    required
                    containerClass="mb-0"
                  />
                </MDBCol>
              </MDBRow>

              <MDBRow className="mt-3">
                <MDBCol md={6}>
                  <MDBInput
                    color="pink"
                    outline
                    label="Date of Birth"
                    group
                    type="date"
                    name="parent2DoB"
                    value={state.parent2DoB}
                    onChange={(e) => {
                      validate();
                      onChangeField(e, e.target.name, e.target.value);
                    }}
                    required
                    containerClass="mb-0"
                  />
                </MDBCol>
                <MDBCol md={6}>
                  <MDBInput
                    label="Place of Birth"
                    group
                    outline
                    type="text"
                    name="parent2PlaceOfBirth"
                    value={state.parent2PlaceOfBirth}
                    onChange={(e) => {
                      validate();
                      onChangeField(e, e.target.name, e.target.value);
                    }}
                    required
                    containerClass="mb-0"
                  />
                </MDBCol>
              </MDBRow>

              <MDBRow className="mt-3 mb-3">
                <MDBCol md={6}>
                  <label>Sex</label>
                  <select
                    color="pink"
                    label="Sex"
                    group
                    className="browser-default custom-select"
                    type="text"
                    name="parent2Sex"
                    value={state.parent2Sex}
                    onChange={(e) => {
                      onChangeField(e, e.target.name, e.target.value);
                    }}
                    required
                    containerClass="mb-0"
                  >
                    <option selected disabled>
                      Choose
                    </option>
                    <option value="male">male</option>
                    <option value="Female">female</option>
                  </select>
                </MDBCol>
                <MDBCol md={6}>
                  <label>US Citizen</label>
                  <select
                    label="US Citizen"
                    group
                    type="text"
                    className="browser-default custom-select"
                    name="parent2CitizenShip"
                    value={state.parent2CitizenShip}
                    onChange={(e) => {
                      onChangeField(e, e.target.name, e.target.value);
                    }}
                    required
                    containerClass="mb-0"
                  >
                    <option selected disabled>
                      Choose
                    </option>
                    <option value="yes">Yes</option>
                    <option value="no">No</option>
                  </select>
                </MDBCol>
              </MDBRow>
              <MDBRow>
                <div class="custom-control custom-checkbox">
                  <input
                    type="checkbox"
                    class="custom-control-input"
                    id="defaultUnchecked"
                    onClick={() => history.push(nextStep)}
                  />
                  <label
                    style={{ fontSize: "16px" }}
                    class="custom-control-label"
                    for="defaultUnchecked"
                  >
                    I do not have this information
                  </label>
                </div>
              </MDBRow>

              <MDBRow center className="d-flex flex-column align-items-center">
                {touched && errorMsg.length > 0 && (
                  <div className="text-danger mb-2">{errorMsg}</div>
                )}
                {touched && !errorMsg && (
                  <Link to={nextStep}>
                    <MDBBtn
                      color="pink"
                      rounded
                      type="button"
                      className="z-depth-1a"
                    >
                      Next
                    </MDBBtn>
                  </Link>
                )}
                {(!touched || errorMsg) && (
                  <MDBBtn
                    color={"grey"}
                    disabled
                    rounded
                    type="button"
                    className="z-depth-1a"
                  >
                    Next
                  </MDBBtn>
                )}
              </MDBRow>
            </MDBCol>
          </MDBRow>
        </form>
      </MDBContainer>
    </Step>
  );
}
